import { Component, OnInit } from '@angular/core';
import { Cliente } from '../../model/client';
import { ClienteService } from '../../service/client.service';

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.css']
})
export class ClientComponent implements OnInit {
  clientes: Array<Cliente>;
  cliSel = null;
  inserir = false;

  constructor(
    private clienteService: ClienteService
  ) {
    this.listarClientes();
  }

  ngOnInit() {

  }

  onSelect(cli: Cliente): void {
    this.cliSel = cli;
    this.inserir = false;
  }

  novoCliente(): void {
    this.cliSel = new Cliente();
    this.inserir = true;
  }

  salvarCliente(): void {
    if (this.inserir) {
      this.clienteService.inserirCliente(this.cliSel)
      .subscribe(() => {
        this.listarClientes();
      });
    } else {
      this.clienteService.atualizarCliente(this.cliSel)
      .subscribe(() => {
        this.listarClientes();
      });
    }
  }

  removerCliente(id: number): void {
    this.inserir = false;
    this.clienteService.removerCliente(id)
    .subscribe(() => {
      this.listarClientes();
    });
  }

  listarClientes(): void {
    this.clienteService.listarClientes()
    .subscribe(clientes => {
        this.clientes = clientes;
        this.cliSel = null;
        this.inserir = false;
    });
  }

  cancelar(): void {
    this.cliSel = null;
    this.inserir = false;
  }
}
